<template>
    <div>
        <!-- 支付弹窗  -->
        <Vtemplate :popuptype="popuptype" v-if="ispopup"></Vtemplate>
        <div class="flex-vertical">  
                <div class="flex-vertical-content bg-grey2">
                    <div class="cartlist">
                        <div class="cartlist-head"> 
                            编辑删除
                        </div>
                        <div>
                            <div class="cartlist-item">
                                <div class="img">
                                    <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1490635373185&di=982f7b407b42f4d2685d7df8d978d6b9&imgtype=0&src=http%3A%2F%2Fpic39.nipic.com%2F20140318%2F2531170_082310004375_2.jpg">
                                </div>
                                <div class="content">
                                    <div>
                                        <div class="title">小米／红米4A 16G版 全网通 4g智能手机</div>
                                        <div class="price">¥ 2000</div>
                                    </div>
                                    <div class="norms">
                                        <div>
                                            商品规格：全网通，白色
                                        </div>
                                        <div class="numhandle">
                                            <span> － </span>
                                            <span class="num"> 999 </span>
                                            <span> ＋ </span> 
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="cartlist-cost"> 
                            <div class="cost-item">
                                <div>
                                    快递费用
                                </div>
                                <div>
                                    ¥ 0
                                </div>
                            </div>
                            <div class="cost-item">
                                <div>
                                    优惠信息
                                </div>
                                <div>
                                    会员优惠
                                </div>
                            </div> 
                            <div class="cost-item">
                                <div>
                                    优惠卷信息
                                </div>
                                <div>
                                    无
                                </div>
                            </div>                                                
                        </div>
                    </div>            
                </div>
                <div class="cartlist-foot">
                    <div class="label">
                        <span class="title">支付金额：</span>
                        <span class="price">
                            ¥ 2135
                        </span>
                    </div>
                    <div class="pay-btn" v-on:click="paySelect"> 
                        支付
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>

<script>

    import Vtemplate from './module/Template.vue'
    export default {
        name:'cartlist',
        components:{
        },
        data() {
            return {
                ispopup:false,
                popuptype:"支付方式"
            }
        },
        components:{
            Vtemplate
        },
        methods:{
            //选择支付方式
            paySelect:function(){
                let self=this
                self.ispopup=true
            }
        },
        mounted(){
            let self=this
            self.loadData(self)
        }       
    }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" >
   @import "../assets/sass/cartlist.scss"
</style>


