import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Index from '@/components/Index'
import Order from '@/components/Order'
import Vip from '@/components/Vip'
import CartList from '@/components/CartList'
import CartDetails from '@/components/CartDetails'


Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    //设置入口 重定向index
		{ path: '/', redirect: '/Main' },
    //主页
    {
      path: '/Main',
      component: Main,
      children : [
        {
          path:'',
          component: Index
        },
        //广信商城
        {
          path:'/index',
          name:'index',
          component: Index
        },
        //我的订单
        {
          path:'/order',
          name:'order',
          component: Order
        },
        //会员中心
        {
          path:'/vip',
          name:'vip',
          component: Vip
        },  
        //列表详情
        {
          path:'/vip',
          name:'vip',
          component: Vip
        },              
      ]
    },
    //购物车列表
    {
      path:'/cartList',
      name:'cartList',
      component: CartList
    }, 
    //商品详情
    {
      path:'/cartdetails/:id',
      name:'cartdetails',
      component: CartDetails
    }, 
  ]
})

export default router


